// mockjs
const Mock = require('mockjs');
//  mock.Random 
const Random = Mock.Random;
const successData = function(data){
    return {status:1,object:data}
}
const failureData = function(msg){
    return {status:1,msg:msg}
}
// mock
const produceNewsData = function() {
    let articles = [];
    let size = Random.integer(1, 8);
    console.info('',size)
    for (let i = 0; i < size; i++) {
        let newArticleObject = {
            title: Random.csentence(5, 30), //  Random.csentence( min, max )
            thumbnail_pic_s: Random.dataImage('300x250', 'mock'), // Random.dataImage( size, text )  Base64 
            author_name: Random.cname(), // Random.cname() 
            date: Random.date() + ' ' + Random.time() // Random.date(),yyyy-MM-ddRandom.time() 
        }
        articles.push(newArticleObject)
    }
 
    return {
        articles: articles
    }
}
//html data
const htmlData = function(){
    let html = [];
    let size = Random.integer(5, 20);
    for(let i = 0;i<size;i++){
        html.push('<div style=\'text-align:center\'><img src=\''+ Random.dataImage('300x250', 'mock'+i)+'\'/><p>'+Random.csentence(5, 15)+'</p></div>');
    }
    return  successData(html.join(''));
}

let gen = function(){
    let size=Random.integer(1,7);
    let data=[];
    let x=[]
    for(let i = 1 ;i<=size;i++){
        x.push(Random.date('M-dd'))
        data.push(Random.integer(2000,4500))
    }
    return {'x':x,'data':data}
}
const growthrecord = function(label){

    let collection = []
    for(let i =0;i<Random.integer(1,8);i++){
        let gen = function(){
            let size=Random.integer(1,7);
            let data=[];
            let x=[]
            for(let i = 1 ;i<=size;i++){
                x.push(Random.date('M-dd'))
                data.push(Random.integer(2000,4500))
            }
            return {'x':x,'data':data}
        }
        collection.push({
            labels: gen().x,
            options:{
                legend: {
                    display: false,
                  }
            },
            datasets: [
              {
                label: label,
                showLabel:false,
                backgroundColor: '#d42930',
                fill:false,
                borderColor:'#d42930',
                borderWidth: 2,
                data: gen().data
              }
            ]
          })
    }
    return successData(collection)
}
const stores = function(){
    let storeD = [];
    for(let i = 0;i<6;i++){
        storeD.push({id:i,name:''+Random.csentence(5, 10)});    
    }
    return successData(storeD)
  
}
const wallet = function(){
    let wallet = {
        card:{label:'',amount:68},
        coupons:{label:'',amount:4},
        score:{label:'',amount:980},
        growthrecord:{show:true},//
        meal:{show:true}, //
        question:{show:true}, //
        evaluate:{show:true} //
    }
    return successData(wallet)
}
const barcode = function(){
    return successData({qrcode:Random.csentence(10,40),timestamp:1234});
}
Mock.setup({
    timeout: '800-1000' //  200  600 '10-100'
});
