
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Card from 'vux/src/components/card/index.vue'

import loadingBox from '@/components/LoadingBox'
export default {
components: {
Blur,loadingBox,
Card,Group, Cell, CellBox
},
data(){
return {
show:{
post:false,
isLoading:true
},
url: 'https://o3e85j0cv.qnssl.com/waterway-107810__340.jpg',
wallet_header:'',
cardImg:'data:image/png;base64,',
couponsImg:'data:image/png;base64,',
scoreImg:'data:image/png;base64,',
wallet:{
card:{label:'',amount:68},
coupons:{label:'',amount:4},
score:{label:'',amount:980},
growthrecord:{show:false},//
meal:{show:false}, //
question:{show:false}, //
evaluate:{show:false} 
}

}
},
mounted(){
this.getFetch()
},
methods:{
getFetch(){
let _this = this;
this.$api.get('/wechat/member/wallet')
.then(function(data){
_this.wallet = data.object
_this.show.post = true;
_this.show.isLoading = false;
})
.catch(this.$errorHandle);
},
showQrcode(){
this.$router.push('/wechat/member/qrcode');
}
}
}



